import React from 'react';
import { useParams } from 'react-router-dom';
import './info.css';

const Info = () => {
    const { slug } = useParams();

    return (
        <div className='info_container'>
            {slug === "terms-of-use" && (
                <>
                    <h1>Terms Of Use</h1>
                    <p>
                        What do you mean by terms of use? Terms of Use are the guidelines and rules set by a website or service provider that users must agree to follow in order to use the service. These terms outline specific aspects like acceptable behavior, disclaimers, or limitations of liability, ensuring users understand their rights and responsibilities while using the website or service. In practice, you can outline rules regarding the use of your content, the suspension of online accounts, or the age limit for purchasing your products. You may have heard about Terms of Use under other names like “Terms and Conditions”, “Terms of Service” or “End User License Agreement” (EULA). Are terms of use a contract? Yes, Terms of Use are a contract between you, the provider of a service, and your users. When users agree to the terms, typically when using the website, buying your products or clicking an “Accept” button, they enter into a contractual agreement with you. This contract binds the user to the terms specified, and violations can lead to termination of service or legal consequences. This contract is formed within applicable law. Some specific requirements are set by consumer laws of different regions in the world, and you should comply with them. As an example, under EU consumer law, consumers have an unconditional right to withdraw (called “cooling-off period”) of 14 days. Is terms of use necessary? A Terms of Use Agreement is not a legal necessity, unlike the privacy policy, which is often mandated by law. However, its importance cannot be overstated. Terms of Use serve as a protective shield for you and your company: They establish legal safeguards for the provider and set clear expectations for users. They help handle potential problems arising or mitigate risks by preventing issues in the first place. Without them, it might be challenging for your business to defend itself effectively in legal disputes concerning your products or services. For these reasons, Terms of Use are considered standard practice and remain an essential document for every business, from individual blog owners to large-scale e-commerce companies. 👋 E-commerce businesses? Listen up! Having a Terms of Use document is a no-brainer for this type of business. In fact, it contains legally-required disclosures regarding conditions of sale, payment methods, shipping, delivery, returns, withdrawals, and more. Learn more here. Where do you put terms of use? You generally put a Terms of Use Agreement on your website, usually in a clearly accessible area. They are often linked at the bottom of the homepage or in the website’s footer. The latter is the recommended practice, since it ensures the document is easy to find and always visible from any page of the site in case users need to refer to it. You can also find the document linked in a menu (that’s usually the case for mobile apps, under sections like “About” or “Legal”), or in the account creation/checkout forms. What is terms of use vs terms and conditions? Terms of Use vs. Terms and Conditions are basically the same thing and refer to the same document. They are used interchangeably, along with other names used to define this type of agreement, like “Terms of Service” or “User Agreement”. They can be used in different contexts but serve the same purpose. Is privacy policy and terms of use the same? A Terms of Use Agreement and a Privacy Policy have very different purposes. Terms of Use outline the rules and conditions for using your website or service, whereas the Privacy Policy describes how personal data is collected, used, and stored. Both are typically linked in the footer of a website, but they refer to unrelated matters. Also, unlike Terms of Use, a Privacy Policy is a legal requirement under many data privacy laws like the EU’s General Data Protection Regulation (GDPR). 💡 Check out our Privacy Policy Template!
                    </p>
                </>
            )}
            {slug === "privacy-policy" && (
                <>
                    <h1>Privacy Policy</h1>
                    <p>
                        What is a Privacy Policy ? A Privacy Policy is a statement or a legal document that states how a company or website collects, handles and processes data of its customers and visitors.It explicitly describes whether that information is kept confidential, or is shared with or sold to third parties. Personal information about an individual may include the following: Name Address Email Phone number Age Sex Marital status Race Nationality Religious beliefs For example, an excerpt from Pinterest's Privacy Policy agreement clearly describes the information Pinterest collects from its users as well as from any other source that users enable Pinterest to gather information from. The information that the user voluntarily gives includes names, photos, pins, likes, email address, and/or phone number etc., all of which is regarded as personal information. Pinterest Privacy Policy: When you give it to us or give us permission to obtain it personal information clause Additionally, Pinterest also states that it collects user location data from mobile devices, and if someone makes a purchase on Pinterest, payment and contact information - including an address and phone number - will be collected.If users buy products or services for others, Pinterest gathers their contact information and shipping details, too. Users may also give Pinterest permission to access information that is shared with other websites like Facebook and Twitter by linking their Pinterest account with them.This information would also include information about their friends and followers.The account settings have information about how much access Pinterest has to their users' data. In sum, a Privacy Policy is where you let your users know all about how you make sure their privacy is respected by your business practices. Why you Need a Privacy Policy Companies or websites that handle customer information are required by law and third parties to publish their Privacy Policies on their business websites.If you own a website, web app, mobile app or desktop app that collects or processes user data, you most certainly will have to post a Privacy Policy on your website(or give in -app access to the full Privacy Policy agreement). Privacy is not a new concept.Humans have always desired privacy in their social as well as private lives.But the idea of privacy as a human right is a relatively modern phenomenon. Around the world, laws and regulations have been developed for the protection of data related to government, education, health, children, consumers, financial institutions, etc. This data is critical to the person it belongs to.From credit card numbers and social security numbers to email addresses and phone numbers, our sensitive, personally identifiable information is important.This sort of information in unreliable hands can potentially have far - reaching consequences. There are several reasons for a website to post its Privacy Policy agreement on its website. Here are some of the main reasons: Required by the law Required by third party services Increases Transparency Let's take a look at each of these reasons in more depth. A Privacy Policy is Required by the Law For individuals to feel comfortable sharing their personal information on the internet, there should be some sort of legal responsibility on businesses to protect that data and keep the users informed about the status and health of their information. Countries around the world have realized the need to protect their citizens' data and privacy. Businesses and websites that collect and/or process customer information are required to publish and abide by a Privacy Policy agreement. A majority of countries have already enacted laws to protect their users' data security and privacy. These laws require businesses to obtain explicit consent from users whose data they will store or process. A few of these laws include the following: CalOPPA in the USA GDPR in the EU PIPEDA in Canada For a business or a website that collects and processes user information in a certain region or country, it is very important to have complete knowledge of the data and privacy protection laws enforced in that region and the region your customers and end users are in.Non - compliance with these laws can result in hefty fines or even prosecution against the violator. In some cases, businesses have to follow laws specific to states or regulations specific to industries. For example, here's how General Motors complies with CalOPPA in the US by including a California-specific section in its Privacy Policy: General Motors California Privacy Policy clause General Motors informs its California users of their rights through its Privacy Policy as required by CalOPPA. If your website / app reaches users around the world, regardless of where you're located or headquartered, you'll need to make sure you follow privacy laws in all applicable countries you reach. While data protection and privacy laws differ from region to region, a Privacy Policy must comprehensively inform its users about how their data will be used. For example, the GDPR is currently the most robust privacy legislation in the world and one of its main requirements for any business that falls under its jurisdiction is to have a GDPR - compliant Privacy Policy that contains some very specific information and is written in an easy - to - understand way. Whether your website is a self - help blog or a game hosted at Google Play, it is your responsibility to give your end users complete information about how any associated third - parties will collect and process their data and(if possible) to what purpose. A Privacy Policy is Required by Third Party Services Apart from governing laws, some websites like Apple, Amazon, and Google require website and app owners to post a Privacy Policy agreement if they use any of their services. Many websites and apps use in -page /in -app advertising by third parties to generate revenue.As these ads also collect user data, third parties require the websites or apps to ask their users' permission for sharing their personal data.
                    </p>
                </>
            )}
            {slug === "about" && (
                <>
                    <h1>About</h1>
                    <p>
                        What Is an About Us Page? The About Us page of your website is an opportunity for you to tell the story of your business, your values, and your achievements. The way you decide to approach this is very important, as the design, visual aspects, and written content showcases how you want your brand to be seen. This is how you can attract customers to your online store who are actually in your target audience. Some key elements of a good About Us page include: A mission statement, vision statement, and company values The company’s backstory Content written in the brand’s tone of voice, which should be decided with the audience in mind Social proof such as testimonials, reviews, awards, or case studies Team information such as people’s roles or personalities Company Values The first key thing to include in your About Us page is a clear and concise mission statement. A good mission statement explains the purpose behind your brand and connects with your audience. Look at mission statement examples for inspiration before starting yours. A mission statement will guide your customers as to whether they think they should shop with you or not. It’s an opportunity to show what’s unique about you and your business. This is also the perfect place to showcase your brands’ vision statement. Another crucial point is outlining your company values. What do you stand for? What kind of image do you want to portray to your customers? This will show the “human” side of your business, and help make it more relatable. Also, customers will be more willing to trust a company that aligns with their own values. Your Story Every single company has a story, so tell yours! Start with the reason behind starting your business, the idea or problem that prompted your first steps, be open about challenges you’ve faced along the way, and pick out some of your favorite moments to share as well. Your customers will want to know about your backstory, and what made you want to start your business in the first place. Talk about your breakthroughs, your milestones, and your evolution of building your brand. Basically, how your business got to become what it is today. Sharing your company story will help your clients to empathize with you and your business. Tone of Voice When telling your story it is important to consider your tone. This should be closely linked to your brand and values (such as using an authoritative, fun, or direct tone), but should also consider your audience and the tone they are most likely to be receptive to. Social Proof If you have some, definitely showcase your past achievements or positive reviews. If others have trusted you in the past and were happy about the outcome, new customers are more likely to trust you themselves! Adding testimonials to your website will also help you build your credibility as a whole, and prove that what you are claiming is true. Introduce the Team Finally, put a face to the business! Introduce the team – if you have one – and the founder. Some classic ways of doing this is to include a photo of each person, along with their role in the business and a short bio, quote, or fun fact! Find Out More If you want more specific tips on How To Write a Bio for a Website then our step-by-step guide has a full breakdown. We crafted a guide specifically to help local businesses. Check out our About Us Page Tips for Local Businesses for more helpful advice.
                    </p>
                </>
            )}
            {slug === "blog" && (
                <>
                    <h1>Blog</h1>
                    <p>
                        When you hear the word “blog”, what do you think of? Maybe your mind goes to stories about travel, yoga, and exciting new restaurants to try. What if I told you that, although these thoughts may be valid, other terms and phrases should be coming to mind? These include conversions, a boost in revenue, calls to action, inbound marketing, and improving customer relationships. Blogs are powerful business tools. They improve conversion rate, foster relationships between your business and audience members and customers, boost revenue, promote brand awareness, increase your ranking on search engines, and positively impact your bottom line. In this guide, we’ll review the different types of blogs there are and examples of each. We've also added a "Niche Industries" section, because there's a blog for everything out there, including your industry. These will give you a better understanding of the various ways your business can write and publish content to help you achieve the benefits we listed above … and more. Any type of business — whether it’s focused on ecommerce, retail, technology, or services — can benefit from publishing and maintaining a blog. By writing about topics that resonate with your audience and incorporating optimization tactics your business can experience a variety of benefits that result from having a blog. Speaking of the benefits of a blog, we mentioned some of them above. Let’s dive into them in more detail. This section will help you answer a commonly asked question around the topic: why blog? Benefits of Blogging There are a variety of reasons why your business should blog. These all explain why it’s a beneficial time investment and addition to your business’s branding, marketing, and sales efforts. Rank on search engines and build authority online so your content and website appear first on the search engine results page, or SERP, when people look up specific terms and keywords. Human Marketing does this well with their mix of blogs and pillar pages (or topic clusters). Drive organic traffic to your website, social media profiles, and other forms of content in a way that feels natural and doesn’t interrupt your audience. Cloud Elements is an example of a company that has seen an increase in organic traffic due to their blog. Educate your leads and customers by helping them stay informed on industry trends and development, and educated about your products, services, and how you can solve their challenges. Contribute to your inbound marketing tactics so you can avoid having your content feel pushy and “salesy”. Grand Rapids Chair is an example of a company that uses their blog to improve their inbound growth. Convert leads and prospects into customers by including helpful and educational information in your blogs so they can understand why your product or service is right for them. Become known as an expert in the industry and stand out against your competition by writing about the various topics your business and product line is known for or relates to. These may include content marketing, public relations, or software for small businesses. 3PL Central is an example of a company who has become known as an industry expert and separates themselves from the competition thanks to their blog. Foster a sense of community so your audience members know there are hundreds (or thousands, depending on the size of your blog) of other people equally as interested in your information and content. Engage audience members and customers so they keep coming back to read your content every day (or as often as you publish your blog) and potentially check out your products or services. Promote your business and products or services by writing about their benefits and linking to information where your readers can learn more … and convert into customers. Boost revenue with calls-to-action (CTAs) and links to your website pages where your audience can learn about and purchase your products or sign up for your services. Support all of your marketing and greater business initiatives by staying consistent with your branding and tone throughout all of your blogs. Now that you have a basic understanding of what blogging is and how it can benefit your business, let’s dive into some examples. You can these examples for inspiration and as a point of reference as you start your business’s blog.
                    </p>
                </>
            )}
            {slug === "faq" && (
                <>
                    <h1>Faq</h1>
                    <p>
                        Frequently Asked Question (FAQ) pages (or informational hubs) enable your business to respond, react, and anticipate the needs of your audience more quickly and appropriately than other types of destination page experiences. An effective FAQ resource can educate, inform, and naturally guide the user through your website’s content and toward the goals and results you have set. Over the years, the role of the FAQ page has changed substantially, and now an FAQ page is an essential webpage to have on your site. Why An FAQ Resource? Firstly, FAQ pages can bring new visitors to your website via organic search and drive them quickly to related pages – most typically deeper blog pages and service pages closely related to the questions being resolved. Next, one of the most significant opportunities for impactful brand visibility within the search engine result pages (in-SERP) is targeting audience questions, wants, needs, and pain points. The FAQ page is one of the best ways to help people visit your site and get snippets of answers in front of users before they click any results within the search pages. A helpful FAQ page (more likely an FAQ hub of core pages and topical intent) shortens the time it takes for people to solve their search requirements. The experience from the first visit to conversion is also faster because you remove any possible barriers to knowledge (informational and often trust). As a company, you are showcasing expertise through FAQs, plus introducing your key staff, knowledge, and unique insights into the industry sooner. You add credibility and value through meaningful content in the many forms your audience requires. This will typically include audio, visual/video, and layering of content types now, compared to traditional text-only content provision. You are also servicing the need for offline conversation and experience through faster and always available online mechanisms. People will always seek help and advice. They are unwilling to pick up the phone, walk into a store, or wait hours (even minutes) for that information or insight to become accessible. It needs to be available now and in the format they enjoy the most. Why FAQ Pages Are A Priority FAQ pages continue to be a priority area for SEO and digital marketing professionals. An FAQ page is one of the simplest ways to improve your site and help site visitors and users. Your FAQ section should be seen as a constantly expanding source of value provided to your audience. It is a place where their ever-changing and growing requirements are not only met but anticipated and exceeded frequently. In no small part, the importance of FAQ pages has been driven in recent years by the growth in voice search, mobile search, and personal/home assistants and speakers. These predominantly rely on the pre-results (Google Answers and Featured Snippets) and can be explicitly targeted with FAQ pages. People need conversation, comparison, and support for most of their decision-making online and offline; FAQs can cater to them all. An effective FAQ page seeks to: Reflect and respond to your audience’s needs wholly and thoroughly. Cover a broad range of intent (transactional, informational, locational, etc.). Stay updated based on new insights from your data, the industry, and broader best practices. Land new users to the website by solving problems and supporting return visits with regular additions and valuable expertise sharing. Drive internal pageviews to other important pages and support key conversion paths. Fuel blog (and deeper content) creation logically and intuitively ties together semantically relevant content. Shine a light on expertise, trust, and authority within your niche, giving your brand and key staff a platform to educate, inform, and support your community.
                    </p>
                </>
            )}

        </div>
    );
};

export default Info;

